@import url(https://fonts.googlapis.com/css?family=Exo);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #343a40;
  overflow-x: hidden;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

body::-webkit-scrollbar {
  display: none;
}

h1, h3, h4 {
  padding-top: 10px;
  padding-bottom: 10px;
}

p {
  margin: 0px;
}

.App {
  background-color: #343a40;
  font-size: 18px;
  /* font-family: Karla, sans-serif; */
  font-family: 'Exo', sans-serif;
  color: #f8f9fa;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.App-page {
  min-height: 80vh;
  align-items: center;
  justify-content: center;
}

.header {
  font-size: 80px;
  color: #ff9100;
}

.shadow {
  box-shadow: 0 10px 30px -10px rgba(2,12,27,0.7);
}

.shade {
  /* box-shadow: -8.49px 8.49px 0px 0px rgba(0, 0, 0, 0.6), 0 0 0 rgba(255, 255, 255, 0.6); */
  box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.8);
}
.home {   
  padding: 100px 0px;
}
  
.animation {
  background: radial-gradient(ellipse at center, rgba(255, 254, 234, 1) 0%, rgba(255, 254, 234, 1) 35%, #ff9100 100%);
  overflow: hidden;
}
  
.ocean {
  height: 5%;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background: #015871;
}
  
.wave {
  background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/85486/wave.svg) repeat-x;
  position: absolute;
  top: -198px;
  width: 6400px;
  height: 198px;
  animation: wave 7s cubic-bezier(0.36, 0.45, 0.63, 0.53) infinite;
  transform: translate3d(0, 0, 0);
}
  
.wave:nth-of-type(2) {
  top: -175px;
  animation: wave 9s cubic-bezier(0.36, 0.45, 0.63, 0.53) -0.125s infinite, swell 7s ease -1.25s infinite;
  opacity: 1;
}
  
.wave:nth-of-type(3) {
  top: -175px;
  animation: wave 4s cubic-bezier(0.36, 0.45, 0.63, 0.3) -0.125s infinite, swell 7s ease -1.25s infinite;
  opacity: 1;
}
  
@keyframes wave {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: -1600px;
  }
}
  
@keyframes swell {
  0%, 100% {
    transform: translate3d(0, -25px, 0);
  }
  50% {
    transform: translate3d(0, 5px, 0);
  }
}
  
.img-hover:hover {
  -webkit-filter: opacity(70%);
          filter: opacity(70%);
}
  
.icon {
  height: 50px;
  width: 50px;
  padding: 5px;
  margin-left:10px;
}
  
@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}
.fade-in {
  animation: fadeIn ease 5s;
  -webkit-animation: fadeIn ease 5s;
  -moz-animation: fadeIn ease 5s;
  -o-animation: fadeIn ease 5s;
  -ms-animation: fadeIn ease 5s;
}
  
.delay-short {
  opacity: 0;
  animation-delay: 1.25s;
  -webkit-animation-delay: 1.25s;
  -moz-animation-delay: 1.25s;
  -o-animation-delay: 1.25s;
  -ms-animation-delay: 1.25s;
  animation-fill-mode: forwards;
}
  
.delay-med {
  opacity: 0;
  animation-delay: 2.5s;
  -webkit-animation-delay: 2.5s;
  -moz-animation-delay: 2.5s;
  -o-animation-delay: 2.5s;
  -ms-animation-delay: 2.5s;
  animation-fill-mode: forwards;
}
h2 {
  padding-top: 50px;
  font-weight: 600;
}
  
hr {
  width: 80%;
  height: 1px;
  border: 0;
  background-image: linear-gradient(to right, rgba(108, 117, 125, 0), rgba(108, 117, 125, 0.75), rgba(108, 117, 125, 0));
}
  
.logo {
  width: 100px;
  height: 100px;
  float: left;
}

.skill {
  margin-bottom: 15px;
}

.skill-name {
  font-size: 20px;
  margin-bottom: 3px;
}

.job-title {
  font-weight: 600;
  font-size: 22px;
  margin-bottom: 3px;
}

.badge {
  font-size: 18px;
  padding: 7px 18px;
  margin-right: 10px;
}

.jobs {
  padding-bottom: 25px;
}
  
.skillset, .work {
  padding-top: 20px;
}
  
.job {
  margin-left: 125px;
}
  
.job-company {
  font-size: 20px;
  margin-bottom: 7px;
}
  
.skill-proficiency, .job-description, .date, .skill-sub {
  font-size: 17px;
  color: #f8f9fabd;
}

.right {
  float: right;
}

.italics {
  font-style: italic;
}
  
.project {
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: 30px;
  /* display: flex; */
  display: grid;
  grid-template-columns: 65% 5% 30%;
  align-items: center;
}

@media only screen and (max-width: 991px) {
  .project {
    padding-top: 20px;
    padding-bottom: 20px;
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
  }
}
  
.project-image {
  padding: 15px;
  /* flex: 0 0 70%; */
  /* max-width: 70%; */
}

.project-title {
  font-weight: 600;
  font-size: 28px;
  margin-bottom: 10px;
  color: white;
}
  
.project-description {
  padding: 30px;
  /* flex: 0 0 30%; */
  /* max-width: 30%; */
  background-color: #6c757d;
  color: #f8f9fac5;
  border-radius: 25px;
  margin: 15px;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
}

.badges {
  margin-top: 5px;
  text-align: center;
}

.badge {
  border-radius: 8px;
  padding: 7px 18px;
  border: 1px solid #50a9d1;
  margin-right: 10px;
  width: auto;
  background-color: #33373a;
}
  
.carousel-caption {
  color: #ff9100;
  top: 30px;
  bottom: auto;
}
  
.other-projects {
  margin-top: 30px;
  margin-bottom: 50px;
}
